import {
  Box,
  Flex,
  Text,
  IconButton,
  Button,
  Stack,
  Collapse,
  Icon,
  Link,
  Popover,
  PopoverTrigger,
  PopoverContent,
  useColorModeValue,
  useBreakpointValue,
  useDisclosure,
  VStack,
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerOverlay,
  Drawer,
} from "@chakra-ui/react";
import {
  HamburgerIcon,
  CloseIcon,
  ChevronDownIcon,
  ChevronRightIcon,
} from "@chakra-ui/icons";
import Skills from "./Skills";
import AboutMe from "./AboutMe";
import ContactMe from "./ContactMe";
import Projects from "./Projects";
import HomePage from "./HomePage";
import { useRef } from "react";

export default function WithSubnavigation() {
  const { isOpen, onToggle } = useDisclosure();

  return (
    <Box>
      <Flex
        bg={useColorModeValue("white", "gray.800")}
        color={useColorModeValue("gray.600", "white")}
        minH={"60px"}
        py={{ base: 2 }}
        px={{ base: 4 }}
        borderBottom={1}
        borderStyle={"solid"}
        borderColor={useColorModeValue("gray.200", "gray.900")}
        align={"center"}
      >
        <Flex flex={{ base: -1 }} justify={{ base: "center", md: "start" }}>
          <Text
            textAlign={useBreakpointValue({ base: "center", md: "left" })}
            fontFamily={"heading"}
            color={useColorModeValue("gray.800", "white")}
            ml={5}
          >
            Logo
          </Text>

          <Flex display={{ base: "none", md: "flex" }} ml={380}>
            <DesktopNav />
          </Flex>
        </Flex>

        {/* <Stack
          flex={{ base: 1, md: 0 }}
          justify={"flex-end"}
          direction={"row"}
          spacing={6}
        > */}
        <Button
          display={{ base: "flex", md: "inline-flex" }}
          fontSize={"sm"}
          fontWeight={600}
          color={"white"}
          bg={"pink.400"}
          href={"#"}
          _hover={{
            bg: "pink.300",
          }}
          ml={340}
          mr={4}
        >
          Resume
        </Button>
        {/* </Stack> */}

        <Flex
          flex={{ base: 1, md: "auto" }}
          ml={{ base: 40 }}
          display={{ base: "flex", md: "none" }}
        >
          {/* <IconButton
            onClick={onToggle}
            icon={
              isOpen ? <CloseIcon w={3} h={3} /> : <HamburgerIcon w={5} h={5} />
            }
            variant={"ghost"}
            aria-label={"Toggle Navigation"}
          /> */}
          <MobileNav />
        </Flex>
      </Flex>

      {/* <Collapse in={isOpen} animateOpacity>
        <MobileNav />
      </Collapse> */}
    </Box>
  );
}

const DesktopNav = () => {
  return (
    <Stack direction={"row"} spacing={8}>
      <HomePage />
      <AboutMe />
      <Projects />
      <Skills />
      <ContactMe />
    </Stack>
  );
};

const MobileNav = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = useRef();
  return (
    <Stack
      bg={useColorModeValue("white", "gray.800")}
      p={4}
      display={{ md: "none" }}
    >
      <Box display={{ base: "flex", md: "none" }}>
        <IconButton
          aria-label="hamburger"
          icon={<HamburgerIcon />}
          ref={btnRef}
          colorScheme="teal"
          onClick={onOpen}
          variant={"ghost"}
          w={3}
          h={3}
          fontSize={30}
        />

        <Drawer
          isOpen={isOpen}
          placement="right"
          onClose={onClose}
          finalFocusRef={btnRef}
        >
          <DrawerOverlay />
          <DrawerContent
            style={{
              backgroundColor: "rgb(14,36,49)",
              marginTop: "60px",
              height: "auto",
            }}
          >
            <DrawerCloseButton color="white" />

            <DrawerBody>
              <VStack
                style={{
                  border: "1px solid red",
                  width: "100px",
                  height: "160px",
                  marginTop: "10px",
                  color: "white",
                }}
              >
                <Box>
                  <Box>
                    <HomePage />
                  </Box>
                  <Box>
                    <AboutMe />
                  </Box>
                  <Box>
                    <Projects />
                  </Box>
                  <Box>
                    <Skills />
                  </Box>
                  <Box>Resume</Box>
                  <Box>
                    <ContactMe />
                  </Box>
                </Box>
              </VStack>
            </DrawerBody>
          </DrawerContent>
        </Drawer>
      </Box>
    </Stack>
  );
};

